@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.page {
  height: 100%;
}
.page-body {
  background-color: #ffffff;
  min-height: 100%;
  margin: 0;
  position: relative;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
.main-header__wrapper {
  position: relative;
  padding-top: 10px;
  margin: 0 auto;
}
.main-header__logo-link {
  display: flex;
  align-items: center;
  width: 191px;
  padding-bottom: 17px;
  padding-left: 20px;
}
.main-header__logo-link:hover {
  opacity: 0.8;
}
.main-header__logo-link:active {
  opacity: 0.6;
}
.main-header__logo-picture {
  margin-right: 57px;
  width: 33px;
  height: 38px;
  display: block;
}
.main-header__logo-text {
  width: 101px;
  height: 18px;
  display: block;
  padding-top: 6px;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 1024px;
    padding-right: 30px;
    padding-left: 30px;
    padding-top: 24px;
    padding-bottom: 30px;
  }
  .main-header__logo-link {
    width: 174px;
    display: block;
    padding: 0;
  }
  .main-header__logo-picture {
    width: 174px;
    height: 52px;
  }
  .main-header__logo-text {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-header__wrapper {
    max-width: 1220px;
    padding-top: 53px;
    margin-bottom: 0;
    padding-bottom: 20px;
  }
  .main-header__logo-link {
    width: 202px;
  }
  .main-header__logo-picture {
    width: 202px;
    height: 59px;
  }
}
.toggle {
  position: absolute;
  top: -2px;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  width: 30px;
  height: 30px;
  padding: 20px;
  padding-bottom: 17px;
  background: 0 0;
  border: 0;
  cursor: pointer;
}
.toggle__button {
  background-repeat: no-repeat;
  background-position: center;
  width: 30px;
  height: 30px;
  top: -2px;
  position: relative;
  display: block;
}
@media (min-width: 768px) {
  .toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle-button {
  background-image: url("/img/vector-images/toggle-button--closed.svg");
}
.main-nav--opened .main-nav__toggle-button {
  background-image: url("/img/vector-images/toggle-button--opened.svg");
}
.main-nav__list {
  position: absolute;
  list-style: none;
  background-color: #FFFFFF;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  z-index: 1;
}
.main-nav__link {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  cursor: pointer;
  text-decoration: none;
  padding-top: 22px;
  padding-bottom: 23px;
  padding-left: 20px;
  padding-right: 20px;
  color: #000000;
  display: flex;
  justify-content: center;
}
.main-nav__item {
  border-top: 1px solid #E7E7E7;
}
.main-nav__item:last-of-type {
  border-bottom: 1px solid #E7E7E7;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    position: static;
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav {
    padding-top: 16px;
    max-width: 74%;
  }
  .main-nav__list {
    position: static;
    display: flex;
    flex-wrap: wrap;
  }
  .main-nav__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    padding: 0;
    padding-bottom: 7px;
  }
  .main-nav__link:hover {
    opacity: 0.6;
  }
  .main-nav__link:active {
    opacity: 0.3;
  }
  .main-nav__item--current .main-nav__link {
    border-bottom: 2px solid #68B738;
  }
  .main-nav__item {
    margin-right: 35px;
    margin-bottom: 10px;
    border: none;
  }
  .main-nav__item:last-of-type {
    margin-right: 0;
    border: none;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    max-width: 48%;
  }
  .main-nav__list {
    background-color: transparent;
  }
  .main-nav__list--index a {
    color: #FFFFFF;
  }
  .main-nav__link {
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 4px;
  }
  .main-nav__list--index .main-nav__item--current {
    border-bottom: 2px solid #FFFFFF;
  }
}
.button {
  text-transform: uppercase;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  padding: 11px 44px;
  color: #FFFFFF;
  background-color: #68B738;
  display: block;
  text-align: center;
  box-sizing: border-box;
  border: none;
}
.button:hover {
  background-color: #5EAA2F;
}
.button:active {
  background-color: #5EAA2F;
  color: rgba(255, 255, 255, 0.3);
  border: none;
}
.button--more {
  background-color: #F2F2F2;
  color: #444444;
  padding-top: 10px;
  padding-bottom: 10px;
}
.button--catalog {
  font-size: 16px;
  line-height: 20px;
  padding: 10px;
}
@media (min-width: 768px) {
  .button {
    width: 244px;
    font-size: 20px;
    line-height: 20px;
    padding: 17px 26px;
  }
  .button--catalog {
    font-size: 16px;
    line-height: 20px;
    padding: 10px;
  }
  .button--more {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
.page-footer__info {
  background-color: #F2F2F2;
}
.page-footer__wrapper {
  text-align: center;
  padding: 39px 20px 22px 20px;
  margin: 0 auto;
  max-width: 600px;
  box-sizing: border-box;
}
.page-footer__logo {
  margin-bottom: 32px;
  display: block;
}
.page-footer__logo:hover {
  opacity: 0.8;
}
.page-footer__logo:active {
  opacity: 0.3;
}
.page-footer__logo-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.page-footer__copyright {
  margin-top: 22px;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1024px;
    padding: 58px 30px 55px 30px;
  }
  .page-footer__logo {
    margin-bottom: 0;
  }
  .page-footer__copyright {
    margin-top: 0;
    max-width: 33%;
  }
}
@media (min-width: 1200px) {
  .page-footer__wrapper {
    max-width: 1220px;
    padding: 0;
    padding-top: 58px;
    padding-bottom: 58px;
  }
}
.contacts__wrapper {
  padding: 26px 20px;
