.social {
  list-style: none;
  margin: 0;
  display: flex;
  justify-content: center;
  padding-left: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #D9D9D9;
  border-top: 1px solid #D9D9D9;
}
.social__item:not(:last-child) {
  margin-right: 27px;
}
.social__link {
  text-decoration: none;
  color: #666666;
}
.social__image {
  display: block;
}
.social__image:hover {
  fill: #68B738;
}
.social__image:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .social {
    padding: 0;
    border: none;
