  width: 50vw;
  min-height: 302px;
  height: 100%;
  background: url("../img/green-bg-right.jpg") repeat-x 0 0;
  background-size: cover;
  left: 50%;
  top: 0;
  z-index: 1;
}
.promo__wrapper {
  background-image: url("/img/background-promo.jpg");
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: center top;
  min-height: 302px;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
  z-index: 3;
}
.promo__header {
