.plan {
  color: #000000;
}
.plan__wrapper {
  max-width: 600px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  padding-top: 10px;
  padding-bottom: 10px;
}
.plan__module {
  background-color: #F2F2F2;
  padding: 30px 20px 19px 20px;
}
.plan__module--slim {
  margin-bottom: 20px;
}
.plan__module-title {
  position: relative;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  padding-left: 71px;
  margin: 0;
  margin-bottom: 29px;
}
.plan__module-title::before {
  content: "";
  position: absolute;
  background-color: #68B738;
  top: -6px;
  left: 2px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.plan__module--slim .plan__module-title::after {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
  background-image: url("../img/vector-images/module-slim-vector.svg");
  width: 36px;
  height: 50px;
  top: -9px;
  left: 9px;
}
.plan__module--pro .plan__module-title::after {
  content: "";
  position: absolute;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-image: url("../img/vector-images/module-pro-vector.svg");
  width: 67px;
  height: 28px;
  top: 5px;
  left: -7px;
}
.plan__module-description {
  font-family: "Arial", "Helvetica CY", "Nimbus Sans L", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  padding-bottom: 22px;
  border-bottom: 1px solid #D9D9D9;
  margin: 0;
  margin-bottom: 19px;
}
.plan__module-link {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  display: inline-flex;
  align-items: center;
  position: relative;
}
.plan__module-link:active {
  opacity: 0.3;
}
.plan__module-link::before {
  content: "";
  background-color: #000000;
  order: 1;
  width: 18px;
  height: 2px;
  margin-left: 16px;
  transition: width 0.3s ease-out;
}
.plan__module-link:hover::before {
  width: 26px;
}
.plan__module-link:active::before {
  opacity: 0.3;
}
.plan__module-link::after {
  content: "";
  order: 2;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 0 5px 6px;
  border-color: transparent transparent transparent #000000;
}
.plan__module-link::after:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .plan__wrapper {
    max-width: 1024px;
    padding: 0 30px;
    margin: 0 auto;
    display: grid;
    row-gap: 30px;
  }
  .plan__module {
    padding: 77px 62px 57px 53px;
    display: flex;
    flex-direction: column;
  }
  .plan__module--slim {
    margin-bottom: 0;
  }
  .plan__module-title {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 24px;
    padding: 0;
    padding-right: 254px;
  }
  .plan__module-title::before {
    width: 200px;
    height: 200px;
    top: 15px;
    left: auto;
    right: 0;
  }
  .plan__module--slim .plan__module-title::after {
    width: 140px;
    height: 194px;
    background-image: url("../img/vector-images/module-slim-tablet.svg");
    top: 4px;
    left: auto;
    right: 30px;
  }
