.checkbox__input {
  cursor: pointer;
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}
.checkbox__label {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #000000;
  cursor: pointer;
  display: block;
  position: relative;
  padding-left: 46px;
  padding-top: 6px;
  padding-bottom: 6px;
}
.checkbox__label:disabled {
  opacity: 0.3;
}
.checkbox__checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 30px;
  width: 30px;
  border: 1px solid #CDCDCD;
  box-sizing: border-box;
}
.checkbox__checkmark:hover {
  border: 1px solid #BABABA;
}
.checkbox__checkmark:disabled {
  border: 1px solid #BABABA;
  opacity: 0.3;
}
/* checkmark/indicator (hidden when not checked) */
.checkbox__checkmark::after {
  content: "";
  position: absolute;
  display: none;
}
.checkbox__label input:checked ~ .checkbox__checkmark::after {
  display: block;
}
/* Style checkmark/indicator */
.checkbox__label .checkbox__checkmark::after {
  left: 2px;
  top: 2px;
  width: 26px;
  height: 26px;
  background-image: url("/img/vector-images/tick-icon.svg");
